<template>
    <div id="work" class="wrapper">

        <div class="static-container">
            <h1 class="title">
                Galery<br v-if="viewport.is568">{<span class="params">'selected'</span>}
            </h1>

            <div class="std">
                <p class="-gray">// Some special projects explained in details.</p>

                <ul class="work-list">
                    <li>
                        <router-link to="/work/diary" title="My Diari">
                            <img src="@/assets/work/work-confianca.png" alt="buku story">
                            <div class="details">
                                <strong>Diary / Story</strong><br>
                                <span class="-comment">// mencoba curhat, tanpa seseorang</span><br>
                                Sekedar catatan saja
                            </div>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/work/foto" title="foto">
                            <img src="@/assets/work/work-admin-panel.png" alt="jepretan">
                            <div class="details">
                                <strong>Hasil jepretan</strong><br>
                                <span class="-comment">// Foto jepretan dengan kamera sendiri</span><br>
                                sekedar jepretan dikala kosong
                            </div>
                        </router-link>
                    </li>
                    <li>
                        <router-link to="/work/karya" title="Karya">
                            <img src="@/assets/work/work-desbravando.png" alt="Hanya karya saya sendiri">
                            <div class="details">
                                <strong>Karya dll</strong><br>
                                <span class="-comment">//sebuah karya </span><br>
                                Mungkin isinya bakal sedikit
                            </div>
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>

    </div>
</template>

<script>
    // styles
    import '@/styles/work.scss';

    export default {
        name: 'work',
        props: {
            viewport: Object,
        },
        data() {
            return {
                intro: new TimelineMax()
            }
        },
        methods: {},
        mounted() {

            /**
             * @desc
             * Intro scene
             */
            this.intro
                .addLabel('enter', 1)
                .from('.title', 2, {
                    autoAlpha: 0,
                    rotationX: 70,
                    transformOrigin: '50% 50% -100px',
                    ease: Power3.easeOut,
                }, 'enter')
                .from('.std', 2, {
                    autoAlpha: 0,
                    x: -32,
                    ease: Power3.easeOut,
                }, 'enter+=1.5');

        },
    };
</script>
